#header{
  width: 100%;

  .inner{
    width: 1180px;
    margin: 0 auto;
    position: relative;

    h1{
      width: 100%;
      padding: 40px 0 10px;
      font: 700 60px/1 'Oswald'; letter-spacing: 5px;
      text-align: center;
      a{
        color: #303030;
      }
    }

    p{
      width: 100%; 
      font: 600 20px/1 'Oswald'; 
      letter-spacing: 5px;
      text-align: center;
      padding-bottom: 40px;
      border-bottom: 4px solid #000;
      position: relative;

      &::after{
        content: ''; display: block; 
        width: 100%; height: 2px; background: #000;
        position: absolute; bottom: -10px; left: 0;
      }
    }

    #gnb{
      width: 100%;
      padding: 15px 80px 10px;
      border-bottom: 2px solid #000;

      ul{
        display: flex; justify-content: space-around; 

        li{
          font: 400 14px/ 1 'Oswald';

          a{
            color: #303030;
          }
        }
      }
    }

    .util{
      position: absolute; top: 140px; right: 0;
      ul{
        display: flex; justify-content: flex-end; 

        li{
          font: 400 14px/1 'Oswald'; 
          margin-left: 20px;
          padding: 2px 5px;
          background: rgb(255, 187, 0);

          a{
            color: #303030;
          }
        }
      }
    }
  }
}

/* tablet */
@media screen and (max-width: $tablet){
  #header{
  
    .inner{
      width: 100%;
  
      h1{
        padding: 30px 0 10px;
        font-size: 40px;
        letter-spacing: 4px;
      }

      p{
        font-size: 18px;
        letter-spacing: 3px;
        padding-bottom: 50px;
      }
  
      #gnb{
        padding: 15px 80px 10px;
  
        ul{
          li{
            font: 400 14px/ 1 'Oswald';
          }
        }
      }
  
      .util{
        position: absolute; top: 120px; right: 3vw;
        ul{
          li{
            font-size: 14px;
            margin-left: 20px;
          }
        }
      }
    }
  }
}

/* moblie */
@media screen and (max-width: $mobile){
  #header{
    .inner{
      h1{
        padding: 30px 0 10px;
        font-size: 26px;
      }

      p{
        font-size: 14px;
        letter-spacing: 2px;
        padding-bottom: 40px;
      }
  
      #gnb{
        padding: 15px 10px 10px;
      }
  
      .util{
        position: absolute; top: 95px; right: 3vw;
      }
    }
  }
}